import { Box, Typography } from "@mui/material"
import Slider from "react-slick";

const HomePage = () => {
  //setting for carousel react-slick
  const settings = {
    autoplay: true,
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    pauseOnHover: true,
    pauseOnDotsHover: true
  };

  return (
    <>
      <Slider {...settings}>
        <div>
          <img src="/images/hero1.jpg" alt="hero" style={{ display: 'block', width: '100%', maxHeight: 500 }} />
        </div>
        <div>
          <img src="/images/hero2.jpg" alt="hero" style={{ display: 'block', width: '100%', maxHeight: 500 }} />
        </div>
        <div>
          <img src="/images/hero3.jpg" alt="hero" style={{ display: 'block', width: '100%', maxHeight: 500 }} />
        </div>
      </Slider>

      <Box display='flex' justifyContent='center' sx={{ p: 4 }}>
        <Typography variant="h1">
          Welcome to the NetStore!
        </Typography>
      </Box>
    </>
  )
}

export default HomePage